<template>
  <div class="hello">
    <input type="text" name="codigo" id="codigo" v-model="codigo" />
    <button @click="imprimir">Imprimir</button>
  </div>
</template>

<script>
export default {
  name: "HelloWorld",
  data() {
    return {
      codigo: "",
    };
  },
  methods: {
    imprimir() {
      // generate page to print
      let conteudo = document.createElement("div");
      let h3 = document.createElement("h3");
      h3.innerHTML = this.codigo;
      conteudo.appendChild(h3);
      let h2 = document.createElement("h2");
      h2.innerHTML = this.codigo;
      conteudo.appendChild(h2);
      // print impressao
      let tela_impressao = window.open("about:blank");
      tela_impressao.document.write(conteudo.innerHTML);
      tela_impressao.window.print();
      tela_impressao.window.close();
      // clean codigo
      this.codigo = "";
      // request focus to codigo input
      document.getElementById("codigo").focus();
    },
  },
};
</script>

